.modalBackground {
    
    right:-10px;
    left:-420;
    top:300px;
    background-color: rgb(139, 201, 241);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index:1
  }
  
  .modalContainer9 {
    width: 1000px;
    height: 450px;
    border-radius: 12px;
    background-color: rgb(234, 236, 240);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    position: absolute;
    
    display: flex;
    flex-direction: column;
    padding: 1rem;
    gap:10px;
  }
  
  .modalContainer .title {
    display: inline-block;
    text-align: center;
    margin-top: 20px;
  }
  
  .titleCloseBtn {
    display: flex;
    justify-content: flex-end;
  }
  
  .titleCloseBtn button {
    background-color: transparent;
    border: none;
    font-size: 25px;
    cursor: pointer;
  }
  
  .modalContainer6 .body9 {
    flex: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    text-align: center;
  }
  
  .modalContainer9 .footer9 {
    position:absolute;
    top:430px;
    left:475px;
    flex: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modalContainer9 .footer9 button {
    
    
    height:35px;
    cursor: pointer;
    width:130px;
    margin-left:-10px;
    color:#ffffff;
    border-radius:10px;
    padding: 5px;
  }
  
