.modalBackground {
    
    right:-10px;
    left:-420;
    top:300px;
    background-color: rgb(139, 201, 241);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index:1
  }
  
  .modalContainer11 {
    width: 350px;
    height: 150px;
    border-radius: 12px;
    background-color: rgb(234, 236, 240);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    position: absolute;
    display: flex;
    flex-direction: column;
    padding: 13px;
  }
  
  .modalContainer11 .title {
    display: inline-block;
    text-align: center;
    margin-top: 20px;
  }
  
  .titleCloseBtn {
    display: flex;
    justify-content: flex-end;
  }
  
  .titleCloseBtn button {
    background-color: transparent;
    border: none;
    font-size: 25px;
    cursor: pointer;
  }
  
  .modalContainer11 .body11 {
    flex: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    text-align: center;
  }
  
  .modalContainer11 .footer11 {
    flex: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
