.app-container {
  display: flex;
  position: relative;
  flex-direction: column;
  gap: 10px;
  padding: 1rem;
  min-height: 100vh;
}
html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}
.browse {
  display: flex;
  align-items: center;
  margin-top: 50px;
  margin-left: 80px;
  justify-content: center;
}

.browse .d-flex {
  align-items: center;
  flex-direction: column;
}

.browse b {
  margin-right: 10px;
}

.custom-container {
  margin-top: 10px;
}

.input {
  margin-right: 10px; /* Adjust this value as needed for spacing */
}

table {
  border-collapse: collapse;
  width: 100%;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
}

th,
td {
  border: 1px solid #ffffff;
  padding: 5px;
  font-size: 22px;
  text-align: center;
  height: 30px;
}

th {
  background-color: #25a1ee;
}

td {
  background-color: rgb(205, 235, 253);
}

form {
  display: flex;
  gap: 5px;
  margin-top: -100px;
}

form td:last-child {
  display: flex;
  justify-content: space-evenly;
}

form * {
  font-size: 15px;
}
.inputBox {
  width: 300px;
}
.mt20 {
  margin-top: 20px;
}
.navbar1 {
  position: absolute;
  margin-left: 100px;
}
.btn {
  width: 60px;
  margin-left: 10px;
  color: #ffffff;
  border-radius: 10px;
  padding: 5px;
}
.btnBlue {
  background-color: #0091ea;
  border: 3px solid #000000;
}
.btnGreen {
  background-color: green;
  border: 2px solid #58f558;
}
.btnGrey {
  background-color: rgb(175, 176, 175);
  border: 3px solid #000000;
}
.btnRed {
  background-color: red;
  border: 2px solid #58f558;
}
.input,
.select {
  padding: 5px 5px;
  border-radius: 4px;
  border: 3px solid #000000;
}

.loader-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.loader-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.spinner {
  width: 3rem;
  height: 3rem;
  border: 4px solid rgba(255, 255, 255, 0.2);
  border-top-color: #ffffff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.loading-text {
  color: white;
  font-size: 1.2rem;
  margin-top: 15px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.text-white {
  color: #fff;
}

/* Adjust the size of the Spinner if needed */
.loader-container .spinner-border {
  width: 3rem;
  height: 3rem;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin-top: 16px;
}
#Dataset {
  height: 30px; /* Adjust height as needed */
  width: 150px;
  margin-top: 16px; /* Adjust width as needed */
}

.button-row label {
  margin-right: 10px;
  margin-top: 18px;
}

.button-row input[type="text"] {
  width: 150px;
  height: 25px;
  margin-top: 16px;
}

.button-row button {
  padding: 8px 16px;
  background-color: #0091ea;
  color: #ffffff;
  border: 2px solid #000000;
  border-radius: 12px;
  cursor: pointer;
  width: 220px; /* Adjust width as needed */
  margin: 15px; /* Add margin for spacing */
}

@media (max-width: 768px) {
  .button-row {
    flex-direction: column;
    align-items: center;
  }
}

.image-wrapper {
  position: relative;
  display: inline-block;
  padding: 20px 0;
}

.image {
  width: 100%;
  height: 300px;
}

.text-overlay {
  position: relative;
  z-index: 2;
  font-size: 30px;
  margin-left: 40px;
  margin-top: -140px;
  color: white;
}

.custom-container .custom-button {
  background-color: #1613b4; /* Custom background color */
  color: white; /* Custom font color */
  border: 2px solid rgb(48, 17, 161); /* Custom border color */
  width: 150px;
}

.custom-container .custom-button:hover {
  background-color: rgb(43, 16, 143); /* Background color on hover */
  color: white; /* Font color on hover */
}

.button-container .custom-generatedatasetbutton {
  background-color: #1613b4; /* Custom background color */
  color: white; /* Custom font color */
  border: 2px solid rgb(48, 17, 161); /* Custom border color */
  width: 150px;
}

.button-container .custom-generatedatasetbutton:hover {
  background-color: rgb(43, 16, 143); /* Background color on hover */
  color: white; /* Font color on hover */
}

.footer {
  background-color: #22bdff;
  padding: 5px 50px;
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
}

.footer p {
  margin: 0;
}
